<template>
	<v-app>
		<v-main>
			<v-card tile height="100%">
				<v-toolbar dark>
					<v-avatar class="mr-2" size="40px">
						<img src="@/assets/app-logo.png">
					</v-avatar>
					<v-toolbar-title>BodhiChat</v-toolbar-title>
					<v-spacer></v-spacer>
					<v-btn icon disabled>
						<v-icon>search</v-icon>
					</v-btn>
					<v-btn icon disabled>
						<v-icon>more_vert</v-icon>
					</v-btn>
					<template v-slot:extension>
						<v-btn icon disabled>
							<v-icon>photo_camera</v-icon>
						</v-btn>
						<v-tabs v-model="tab" grow>
							<v-tabs-slider color="yellow"></v-tabs-slider>
							<v-tab v-for="item in tabs" :key="item.text" :disabled="item.disable">{{ item.text }}</v-tab>
						</v-tabs>
					</template>
				</v-toolbar>
				<v-tabs-items v-model="tab">
					<v-tab-item v-for="item in tabs" :key="item.text">
						<v-card flat>
							<v-list two-line>
								<template v-for="(contact, index) in contacts">
									<v-list-item :key="index" @click="open_drawer(contact)">
										<v-list-item-avatar>
											<v-img :src="get_contact_avatar(contact.avatar)"></v-img>
										</v-list-item-avatar>
										<v-list-item-content>
											<v-list-item-title>{{ contact.name }}</v-list-item-title>
											<v-list-item-subtitle>{{ get_last_conversation(contact.blocked) }}</v-list-item-subtitle>
										</v-list-item-content>
									</v-list-item>
									<v-divider :key="`${contact}_${index}`" inset v-if="contacts.indexOf(contact) !== contacts.length - 1"></v-divider>
								</template>
							</v-list>
						</v-card>
					</v-tab-item>
				</v-tabs-items>
				<bd-navigation-drawer v-model="drawer" :name="current_contact.name" :avatar="current_contact.avatar" :blocked="current_contact.blocked"></bd-navigation-drawer>
			</v-card>
		</v-main>
	</v-app>
</template>

<script>
import BdNavigationDrawer from "@/components/bd-navigation-drawer";
import 'material-icons/iconfont/filled.css';
import {conversation} from "./store/Conversation";

export default {
	name: 'App',

	components: {
		BdNavigationDrawer
	},

	data: () => ({
		tab: 0,
		tabs: [
			{text: '對話', disable: false},
			{text: '動態', disable: true},
			{text: '通話', disable: true},
		],
		drawer: false,
		contacts: [
			{
				name: 'Cyber佛',
				avatar: 'buddha',
				blocked: false,
			},
			{
				name: '耶穌',
				avatar: 'jesus',
				blocked: true,
			}
		],
		current_contact: {
			name: '',
			avatar: '',
		},
		conversation: conversation.list,
	}),
	methods: {
		get_contact_avatar(avatar) {
			if(avatar !== ''){
				return require(`@/assets/${avatar}-icon.png`);
			}
		},
		get_last_conversation(blocked) {
			if (conversation.list.length === 0 || blocked) {
				return '';
			}
			return conversation.list[conversation.list.length - 1].text;
		},
		open_drawer(contact) {
			this.drawer = true;
			this.current_contact = contact;
		}
	}
};
</script>
